*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Helvetica Neue", sans-serif;
}

#root {
  width: 100%;
  height: 100%;
}

button, input[type="submit"], input[type="reset"] {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

input {
  font: inherit;
}
.Message {
  position: relative;
  padding: 6px 6px 6px 36px; }
  .Message__avatar {
    width: 36px;
    height: 36px;
    border-radius: 4px;
    position: absolute; }
  .Message__time {
    font-size: 12px;
    color: #717274;
    display: block; }
  .Message__body {
    margin: 0 48px;
    min-height: 36px; }
  .Message__text {
    color: #222222;
    font-size: 15px; }

.Loader {
  -webkit-animation: rotating 1s ease-in-out infinite;
          animation: rotating 1s ease-in-out infinite;
  -webkit-transform-origin: center;
          transform-origin: center; }
  .Loader > svg {
    display: block; }

@-webkit-keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotating {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.Chat__aside-overlay, .Chat__main-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center; }

.Chat {
  display: flex;
  height: 100%; }
  .Chat__aside {
    width: 240px;
    background-color: #f56b2a;
    position: relative; }
  .Chat__channel-btn {
    color: #fff;
    display: block;
    width: 100%;
    text-align: left;
    padding: 12px 16px; }
    .Chat__channel-btn--selected {
      background-color: rgba(0, 0, 0, 0.3); }
    .Chat__channel-btn:hover {
      background-color: rgba(0, 0, 0, 0.2); }
    .Chat__channel-btn::before {
      content: "#"; }
  .Chat__main {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    position: relative; }
  .Chat__messages {
    flex: 1 1;
    overflow-y: auto;
    padding-bottom: 20px; }
  .Chat__input-wrapper {
    display: flex; }
  .Chat__input {
    width: 100%;
    border-radius: 8px;
    border: 2px solid #717274;
    height: 40px;
    margin: 0 20px 20px 20px;
    padding: 6px 10px;
    outline-style: none;
    font-size: 14px; }
    .Chat__input::-webkit-input-placeholder {
      color: #c0c0c0; }
    .Chat__input::-ms-input-placeholder {
      color: #c0c0c0; }
    .Chat__input::placeholder {
      color: #c0c0c0; }
  .Chat__aside-overlay {
    background-color: #f56b2a;
    font-size: 32px;
    color: #fff; }
  .Chat__main-overlay {
    background-color: #fff;
    font-size: 64px; }

